@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
:root {
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn:focus, .btn-primary:focus, button:focus {
  box-shadow: none;
}

.name-selector {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.header-link--title {
  color: white;
  font-size: 3rem;
}

.header-link--title:hover {
  text-decoration: none;
  color: aquamarine;
}

.header-link--sideText {
  color: white;
  font-size: 1.5rem;
  margin-left: 1em;
}

.header-link--sideText-active {
  color: white;
  font-size: 1.5rem;
  margin-left: 1em;
  text-decoration: underline;
}

.header-link--sideText:hover {
  text-decoration: none;
  color: aquamarine;
}

#members {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 1em;
  font-size: 1rem;
  border: 5px solid #007cff;
  min-width: 20em;
  min-height: 25em;
  margin-top: 5em;
}

#members > input {
  height: 2em;
  font-size: 1.5rem;
  width: 100%;
  outline: none;
  border-radius: 0.1em;
  border: 1px solid gray;
  margin-bottom: 0.2em;
  padding-left: 1em;
  padding-right: 1em;
}

#members button {
  width: 5em;
  align-self: flex-end;
  margin-bottom: 1em;
}

#members h2 {
  font-size: 1.2rem;
  font-weight: bold;
}

.member-item {
  display: flex;
  align-items: center;
  height: 2em;
  width: 100%;
  margin-bottom: 0.5em;
}

.member-item__checkbox-container {
  width: 2em;
  height: 100%;
  border: 1px solid grey;

  border-top-left-radius: 0.3em;
  border-bottom-left-radius: 0.3em;
  background-color: gray;
  cursor: pointer;
}

.member-item__checkbox-container:hover {
  background: #0068d9;
  border-color: #0068d9;
}

.member-item__checkbox-container .tick {
  font-size: 1.5rem;
  height: 100%;
  position: relative;
  bottom: 0.1em;
  color: white;
}

.member-item__name {
  height: 100%;
  border: 1px solid grey;
  border-left: 0;
  flex-grow: 1;
  line-height: 2em;

  border-top-right-radius: 0.3em;
  border-bottom-right-radius: 0.3em;
}

.member-item__delete {
  color: red;
  min-width: 2rem;
  font-size: 1.5em;
  cursor: pointer;
}

.member-item__delete:hover {
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  text-shadow: 2px 2px yellow;
}

.name-selector__display {
  font-family: lato;
  font-size: 5rem;
  color: black;
  font-weight: bold;
  line-height: 1.2em;
  margin-top: 1.2em;
  margin-bottom: 0.5em;
}

.name-selector__button {
  width: 20em;
  margin-bottom: 1rem;
}

.name-history {
  font-size: 1em;
  border: 5px solid #007cff;
  width: 20em;
  min-height: 15em;
  margin-left: auto;
  margin-right: auto;
}

.name-history__title {
  font-size: 1.2em;
  font-weight: bold;
  margin: 1em;
}

.pair-names {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  border: 0.25em dotted rebeccapurple;
  border-radius: 4px;
  font-size: 1em;
  margin: 1em;
}

.pair-names * {
  margin: 1em 0
}

.pair-names * span {
  margin: 0.5em
}

.pair-names .left {
  text-align: right;
}

.pair-names .right {
  text-align: left;
}

.pair-names .connector {
  color: rebeccapurple;
  font-size: 0.8em;
}

.pairs__title {
  font-family: lato;
  font-size: 5rem;
  color: black;
  font-weight: bold;
  line-height: 1.2em;
  margin-top: 1.2em;
  margin-bottom: 0.5em;
}

.pairs__pairedNames {
  font-family: lato;
  margin-top: 5em;
}

.pairs__button {
  width: 20em;
}

.section__button {
  margin: 1em;
}

.pairs-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.pairs-container * {
  flex: 40% 1;
}
.timer--set {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer--set--text {
  width: 1.5em;
  border: none;
  text-align: center;
  padding: 0;
}

.timer--set--icon {
  display: inline-block;
  font-size: 1rem;
  height: 1em;
  line-height: 0.9em;
  width: 1em;
  color:  rgba(66, 98, 255, 1);
  background-color: rgba(66, 98, 255, 0.12);
  border-radius: 0.5em;
  cursor: pointer;
}

.timer--control--icon {
  font-size: 1rem;
  display: inline-block;
  height: 1em;
  line-height: 0.9em;
  width: 1em;
  color:  white;
  background-color: rgba(66, 98, 255);
  border-radius: 0.5em;
}

.timer--control {
  border-left: 1px solid rgba(66, 98, 255);
  margin-left: 1em;
  padding-left: 1em;
}
